import { Component, Output, EventEmitter } from '@angular/core';

import { ProductDataService } from './product-data.service';

@Component({
  selector: 'app-production-details',
  templateUrl: './production-details.component.html',
  styleUrls: ['./production-details.component.css']
})
export class ProductionDetailsComponent {
  productDetails: any;

  title = 'AngularStore';
  @Output() messageEvent = new EventEmitter<number>();
  showCartData: boolean;
  tcode: string;
  cartCounter: number = 0;
  addCart: any[] = [];

  constructor(private ProductDataService: ProductDataService) { }

  ngOnInit() {
    this.ProductDataService.getProductData().subscribe(
      (data1: any[]) => {
        this.productDetails = data1;
      })
  }



  addToCart(_id, _name, _supplier, _quantity, _unit_cost) {
    let temp = new Object();
    temp["id"] = _id;
    temp["name"] = _name;
    temp["supplier"] = _supplier;
    temp["quantity"] = _quantity;
    temp["unit_cost"] = _unit_cost;
    this.addCart.push(temp);
    console.log(this.addCart);
    this.cartCounter++
    // this.messageEvent.emit(this.cartCounter);




  }
  showData() {

    this.showCartData = true;
  }

}





